:root {
  --accent-color: #5efc56;
}

.App {
  background-color: #111;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 8px; /* I want to add padding */
  box-sizing: border-box;
}

main {
  margin-top: 70px;
  width: 100%;
  height: 100%;
}

.appbar-label {
  position: absolute;
  top: 0px;
  right: 10px;
  height: 60px;
  display: flex;
  align-items: center;
  text-align: center;
}

.drawer {
  background-color: #333 !important;
}

.top-bar {
  width: 100%;
  height: 60px;
  background-color: #333;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.skills {
  display: flex;
  justify-content: center;
  width: 100%;
}

.skillsInner {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
}

.nav-link {
  color: white;
  font-size: 20px;
  text-decoration: none;
}

.skill {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 80px;
  height: 100%;
  border-radius: 16px;
  background-color: #292929;
  text-align: center;
}

.project {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 100vh;
  background-color: #444;
  text-align: center;
}


.skill-content {
  display: flex;
  align-items: center;
  margin-left: 12px;
}

.skill-content p {
  margin-left: 16px;
  font-size: 20px;
  text-align: left;
  width: 100%;
}

.drawerActivator {
    position: absolute;
    top: 0px;
    left: 8px;
    background: transparent;
    border: none;
    color: white;
    font-size: 30px;
    width: 40px;
  }

.ButtonRow {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  width: 100%;
}

.ButtonRowInner {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 12px;
}

@media (min-width: 768px) {
  .ButtonRowInner {
    width: 400px
  }

  .skillsInner {
    width: 40%;
  }
}

button {
  border-radius: 16px;
  border: none;
  background-color: var(--accent-color);
  width: 100%;
  height: 60px;
  font-size: 16px;
  font-weight: bold;
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
  text-align: center;
}